import { Component, OnInit } from '@angular/core';
import { X21ServiceDispatcherService } from '@x2one/core';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-manage-numbers',
  templateUrl: './manage-numbers.component.html',
  styleUrls: ['./manage-numbers.component.css']
})
export class ManageNumbersComponent implements OnInit {
  options = {};
  data = [];
  displayedColumns = ['active', 'number', 'incomingCalls', 'outgoingCalls', 'incomingSms', 'outgoingSms', 'createdAt']
  constructor(private dispatcher: X21ServiceDispatcherService, private _snackBar: MatSnackBar) { }

  ngOnInit() {
  }

  setStatus(message) {
    this._snackBar.open(message, "ok", {
      duration: 3000, verticalPosition: 'top'
    });
  }
  validateNumber() {
    if (this.options["number"] != undefined) return true
    else { this.setStatus("לא הוכנס מספר") }
  }

  addNumber() {
    if (this.validateNumber()) {
      this.dispatcher.dispatch('http://0.0.0.0:3000/api/v1/number_list/add', this.options).subscribe((response) => {
        this.setStatus("!המספר נוסף בהצלחה")
        this.clear()
      },
        (error) => {
          console.log(error)
          this.setStatus("בעיה בהוספת המספר")
        }
      )
    }
  }

  clear() {
    this.options = {}
  }

  search(active: boolean = false) {
    if (this.validateNumber()) {
      this.dispatcher.dispatch('http://0.0.0.0:3000/api/v1/number_list/get', this.options).subscribe((response) => {
        this.data = response.data('data')
      },
        (error) => {
          console.log(error)
          this.setStatus("בעיה בחיפוש")
        }
      )
    }
  }

  checkNumber() {
    if (this.validateNumber()) {
      this.dispatcher.dispatch('http://0.0.0.0:3000/api/v1/number_list/check_number', this.options).subscribe((response) => {
        this.data = response.data('data')
        if (this.data.length < 1) {
          this.data.push({ number: this.options["number"], active: true, outgoing_calls: true, incoming_calls: true, incoming_sms: true, outgoing_sms: true })
        }
      },
        (error) => {
          console.log(error)
          this.setStatus("בעיה בבדיקה")
        }
      )
    }
  }

}
